import React,{Fragment} from "react"
import { Link,useParams } from "react-router-dom"
import style from "./commonMovieData.styl"
import CommonLabel from "../commonLabel/CommonLabel";
import PropTypes from "prop-types"

interface celebrityType{
	id:number;
	name:string;
}

interface CommentsMovieDataType{
	id:number;
	cover:string;
	type:string;
	country:string;
	timeLen:number;
	spoiler:number;
	time:string;
	name:string;
	director:celebrityType[];
	toStar:celebrityType[];
	language?:string;
}


interface PropsType{
	CommentsMovieData:CommentsMovieDataType
}

const CommonMovieData:React.FC<PropsType> = (props) => {
	const params = useParams()
	const { movie_id } = params

	const { CommentsMovieData } = props
	return(
		<Fragment>
			<div className={style.backLink}><Link to={"/detail/"+ movie_id}>  { CommentsMovieData.name}</Link></div>
			<div className={style.movieDetail}>
				<div className={style.coverWrapper}><img src={CommentsMovieData.cover} alt=""/></div>
				<CommonLabel label="" content={CommentsMovieData.director} pathName="celebrity"/>
				<CommonLabel label="" content={CommentsMovieData.toStar} pathName="celebrity"/>
				<CommonLabel label="" content={CommentsMovieData.type}/>
				<CommonLabel label="/" content={CommentsMovieData.country}/>
				<CommonLabel label="" content={CommentsMovieData.language}/>
				{ CommentsMovieData.time && <CommonLabel label="" content={timeFormatChange(CommentsMovieData.time)}/>}
				<CommonLabel label="" content={CommentsMovieData.timeLen}/>
			</div>
		</Fragment>
	)

	function timeFormatChange(time: string){
		return time.substring(0,10)
	}
}



export default CommonMovieData
